import * as types from './actionTypes'

const initState = {
    quiz : [],
    isLoading: false,
    isError: false,
}

export const reducer = (state = initState,action) => {
    const {type,payload} = action

    switch(type) {
        case types.GET_QUIZ_REQUEST : {
            return {
                ...state,
                isLoading : true,
                isError : false
            }
        }
        case types.GET_QUIZ_SUCCESS : {
            return {
                ...state,
                quiz : payload,
                isLoading : false,
                isError : false
            }
        }
        case types.GET_QUIZ_FAILURE : {
            return {
                ...state,
                isLoading : false,
                isError : true
            }
        }
        default :
        return state

    }

}