import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Radio, RadioGroup, Stack } from "@chakra-ui/react";
import { getData } from "../Redux/action";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

const Quiz = () => {
  const quiz = useSelector((store) => store.quiz.results);
  const dispatch = useDispatch()
  const nav = useNavigate()

  const [value, setValue] = React.useState("1");

  console.log(quiz);
  const localData = JSON.parse(localStorage.getItem("page14"))

  const [count,setCount] = useState(1)
  const [inCount,setIncount] = useState(1)
  const [page,setPage] = useState(1)


  const handleNext = () => {
    setPage(page+1)
    if(value == quiz[0].correct_answer)
    {
        alert("YOUR ANSWER IS CORRECT")
        setCount(count+1)
        localStorage.setItem("mock14",JSON.stringify(count))
    }
    else if(value != quiz[0].correct_answer){
        alert("YOUR ANSWER IS INCORRECT")
        setIncount(inCount + 1)
        localStorage.setItem("Inmock14",JSON.stringify(inCount))

    }
    if(page< localData.no_of ){
        dispatch(getData(localData.category,localData.level))
    }
    if(page >= localData.no_of ){
        var btn = document.getElementById("nextBtn");
      btn.innerHTML = "Now you can Submit your answer";
    //   nav("/results")

    }
    

  }
  const handleSubmit = () => {
    nav('/results')
  }

  


  return (
    <div>
        <div style={{textAlign:"left"}}>
            <Button onClick={handleSubmit}>Submit Your Quiz</Button>
        </div>
      {quiz?.map((item) => (
        <div>
          <h3>{item.category}</h3>
          <br />
          <br />
          <br />
          <h1
            style={{ fontWeight: "bold" }}
          >{`QUESTION :- ${item.question}`}</h1>
          <br />
          <br />

          <div>
            <RadioGroup onChange={setValue} value={value}>
                
              
                <Radio value={item.correct_answer}>{item.correct_answer}</Radio>
                <Radio value={item.incorrect_answers[0]}>{item.incorrect_answers[0]}</Radio>
                <Radio value={item.incorrect_answers[1]}>{item.incorrect_answers[1]}</Radio>
                <Radio value={item.incorrect_answers[2]}>{item.incorrect_answers[2]}</Radio>
              
            </RadioGroup>
          </div>
        </div>
      ))}

      <br />
      <Button variant="outline" onClick={handleNext} id="nextBtn" >NEXT</Button>
      <br />
      {/* <Button variant="outline" onClick={handleCheck} id="nextBtn" >Check your answer</Button> */}
    </div>
  );
};

export default Quiz;
