import React from "react";
import { Button, Input, Select, Stack, Text } from "@chakra-ui/react";
import { useState } from "react";
import { useDispatch, useSelector } from 'react-redux'
import { getData } from "../Redux/action";
import { useNavigate } from "react-router-dom";

const Homepage = () => {
    const quiz = useSelector((state) => state)
    const dispatch = useDispatch()
    const nav = useNavigate()

    const [name,setName] = useState("");
    const [category,setCategory] = useState("")
    const [level,setLevel] = useState("")
    const [no_of, setNo_of] = useState("")

    const localData = {
        name,
        no_of,
        category,
        level
    }
    

    const handleClick = async () => {
        localStorage.setItem("page14",JSON.stringify(localData))
        localStorage.setItem("mock14",JSON.stringify(0))
        localStorage.setItem("Inmock14",JSON.stringify(0))
        
        await dispatch(getData(category,level))

        nav('/quiz')
        
        
    }
    // console.log(quiz)
  return (
    <>
      <Text fontSize="4xl">Setup Your Quiz</Text>
      <br />
      <Stack spacing={3} margin="auto">
        <Input placeholder="Enter Your Name" size="lg" width="lg" variant='filled' onChange={(e) => setName(e.target.value)} />

        <Select placeholder="Select Category"  width="lg" variant='filled' onChange={(e) => setCategory(e.target.value)}>
          <option value="9">General Knowledge</option>
          <option value="21">Sports</option>
          <option value="22">Geography</option>
        </Select>

        <Select placeholder="Select Difficulty"  width="lg" variant='filled' onChange={(e) => setLevel(e.target.value)}>
          <option value="easy">Easy</option>
          <option value="medium">Medium</option>
          <option value="hard">Hard</option>
        </Select>

        <Input placeholder="Choose No of Question" size="lg" width="lg" variant='filled' onChange={(e) => setNo_of(e.target.value)} />
        <br />
        <Button colorScheme='red' width="lg" onClick={handleClick}>START QUIZ</Button>
      </Stack>
      
    </>
  );
};

export default Homepage;
