import React from 'react'

const Result = () => {
    const correct = JSON.parse(localStorage.getItem("mock14"))
    const Incorrect = JSON.parse(localStorage.getItem("Inmock14"))
    const total = JSON.parse(localStorage.getItem("page14"));

    let percentage = (correct/total.no_of) * 100
    // console.log(percentage)
  return (
    <div>
        <h1>{`HELLO ${total.name}`}</h1>
        <br />
        <h1>{`Your Correct answer count is ${correct}` }</h1>
        <br />
        <h1>{`Your Incorrect answer count is ${Incorrect}` }</h1>
        <br />
        <h1>{`Total Score is ${correct}/${total.no_of}`}</h1>
        <br />
        <h1>{`TOTAL Percentage You Got is ${percentage}`} </h1>
    </div>
  )
}

export default Result