import * as types from './actionTypes'
import axios from 'axios'

const getQuizReq = () => {
    return {
        type : types.GET_QUIZ_REQUEST
    }
}

const getQuizSuccess = (payload) => {
    return {
        type : types.GET_QUIZ_SUCCESS,
        payload
    }
}

const getQuizFailure = () => {
    return {
        type : types.GET_QUIZ_FAILURE
    }


}

export const getData =  (category,difficulty) => {
    return  (dispatch) => {
        dispatch(getQuizReq());
         axios.get(`https://opentdb.com/api.php?amount=1&category=${category}&difficulty=${difficulty}`).then((res) => dispatch(getQuizSuccess(res.data))).catch((e) => dispatch(getQuizFailure()))              

    }
}

